@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;400&display=swap);
* {
    margin: 0;
    padding: 0;

    font-family: "Raleway", sans-serif;
    color: #4a486b;
    color: var(--theme-purple);
}

header {
    height: 75px;
}

article {
    /*To account for both the headers size and also its top and bottom padding*/
    padding-top: calc(75px + 3%);
    min-height: 35vh;
}

:root {
    --theme-purple: #4a486b;
    --theme-light-purple: #6a6981;
    --theme-complimentary: #79aab1;
}

@media only screen and (max-width: 400px) {
    main {
        margin-top: 15% !important;
        margin-bottom: 10% !important;
    }
}

@media only screen and (max-width: 300px) {
    main {
        margin-top: 25% !important;
        margin-bottom: 10% !important;
    }
}

:root{--width: 175px;--height: calc(calc(calc(var(--width) / 16) * 9) * 2)}.link{text-decoration:none}.postCard{border:1px solid rgba(0,0,0,0.37);color:black;cursor:pointer;width:calc(175px * 2);width:calc(var(--width) * 2);height:calc(calc(calc(calc(175px / 16) * 9) * 2) * 2);height:calc(var(--height) * 2);display:grid;grid-template-rows:1fr 1fr}.postCard .postPreview img{width:100%}.postCard .postPreview iframe{width:100%;height:100%}.postCard .postCardText{display:flex;justify-content:center;flex-direction:column;padding:25px}.postCard .postCardText h3{margin-bottom:5px;font-size:1em;font-style:italic}@media only screen and (max-width: 425px){:root{--width: 125px}.postCard h2{font-size:1em}.postCard h3{font-size:0.75em}.postCard p{font-size:0.65em}}

