.postsMain h1{font-size:42px;font-weight:200;text-align:center;margin-top:5%}.postsMain #postCardGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(352px, 1fr));place-items:center;grid-gap:50px;gap:50px}.postsMain .hidden{visibility:hidden;order:99999;min-height:0px !important;min-width:0px !important;height:0px !important;width:0px !important;padding:0px !important;margin:0px !important}.postsMain .hidden *{height:0px !important;width:0px !important;padding:0px !important;margin:0px !important}.postsMain .categoryControls{margin-top:2.5%;margin-bottom:2.5%;text-align:center}.postsMain .categoryControls #searchBox{height:35px;width:calc(30% + 30px);border-radius:10px;border:1px solid var(--theme-light-purple);text-indent:5px}

.postMain{--main-margin: 25%;margin-top:7.5%}.postMain .title{margin-left:var(--main-margin);margin-right:var(--main-margin);margin-top:5%}.postMain .title h1{font-size:48px}.postMain .title h2{font-weight:300;font-size:28px}.postMain .title h4{font-weight:300;font-style:oblique;font-size:14px}.postMain .title h3{color:rgba(0,0,0,0.609);font-weight:300}.postMain .title .react-share__ShareButton{padding:0.5% !important}.postMain img{width:calc(100% - var(--main-margin) * 2);margin-left:var(--main-margin);padding-top:5%;padding-bottom:5%}.postMain #videoContainer{position:relative;padding-bottom:56.25%;margin-bottom:calc(-56.25% + calc(100% - calc(var(--main-margin) * 2)) / 1.5);margin-top:5%;height:0}.postMain #videoContainer iframe{position:absolute;top:0;left:0;width:calc(100% - calc(var(--main-margin) * 2));height:calc(100% - calc(var(--main-margin) * 2));margin-left:var(--main-margin)}.postMain .postBody{margin-left:var(--main-margin);margin-right:var(--main-margin);padding-bottom:5%;word-wrap:break-word}.postMain .postBody *{color:black !important}.postMain .postBody p{font-size:17px}.postMain .postBody ol li{margin-bottom:1em}@media only screen and (max-width: 900px){.postMain{--main-margin: 12.5%}}@media only screen and (max-width: 750px){.postBody p{font-size:16px !important}}

.notFoundMain {
    display: grid;
    place-items: center;

    height: 400px;

    width: 80%;
    margin-left: 10%;
}

.notFoundMain a {
    cursor: pointer;
    text-decoration: underline;
}

.notFoundMain a:hover {
