}

header{position:fixed;top:0;left:0;z-index:999;width:100%;display:flex;place-items:center;justify-content:space-between;padding-top:1%;padding-bottom:2%;background-color:white;border-bottom:1px solid rgba(0,0,0,0.233);box-shadow:0 2px 4px 0 rgba(60,64,67,0.356)}header #title{margin-left:5%}header #title a{text-decoration:none;color:var(--theme-purple)}header h1{margin-bottom:0%;font-weight:300;font-size:38px;width:100%}header h2{font-weight:300;font-size:18px}header .burgerContainer{z-index:2;cursor:pointer;float:right;margin-right:5%}header .bar1,header .bar2,header .bar3{width:35px;height:5px;background-color:#333;margin:6px 0;transition:0.4s}header .change .bar1{transform:rotate(-45deg) translate(-9px, 6px)}header .change .bar2{opacity:0}header .change .bar3{transform:rotate(45deg) translate(-8px, -8px)}#nav{display:none;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:white}#nav #navContent{width:100%;height:100%;display:grid;place-items:center}#nav #navContent ul{list-style:none;display:grid !important;place-items:center}#nav #navContent ul li{cursor:pointer;font-weight:200;font-size:36px;margin-bottom:5%}#nav #navContent ul li a{text-decoration:none;color:black}#nav #navContent ul li a:hover{color:grey}.navShow{display:block !important}@media only screen and (max-width: 500px){header #title h1{font-size:30px}header #title h2{font-size:14px}}@media only screen and (max-width: 400px){header #title h1{font-size:25px}header #title h2{font-size:10px}}@media only screen and (max-width: 300px){header #title h1{font-size:20px}header #title h2{font-size:10px}}

#mc_embed_signup {
    background-color: var(--theme-complimentary);
    padding: 2.5%;
    margin-top: 10%;
}

#mc-embedded-subscribe {
    background-color: var(--theme-purple) !important;
}

#mc-embedded-subscribe:hover {
    background-color: var(--theme-light-purple) !important;
}

/* MailChimp Form Embed Code - Slim - 12/15/2015 v10.7 */
#mc_embed_signup form {display:block; position:relative; text-align:left; padding:10px 0 10px 3%}
#mc_embed_signup h2 {font-weight:bold; padding:0; margin:15px 0; font-size:1.4em;}
#mc_embed_signup input {border:1px solid #999; -webkit-appearance:none;}
#mc_embed_signup input[type=checkbox]{-webkit-appearance:checkbox;}
#mc_embed_signup input[type=radio]{-webkit-appearance:radio;}
#mc_embed_signup input:focus {border-color:#333;}
#mc_embed_signup .button {clear:both; background-color: #aaa; border: 0 none; border-radius:4px; letter-spacing:.03em; color: #FFFFFF; cursor: pointer; display: inline-block; font-size:15px; height: 32px; line-height: 32px; margin: 0 5px 10px 0; padding:0; text-align: center; text-decoration: none; vertical-align: top; white-space: nowrap; width: auto; transition: all 0.23s ease-in-out 0s;}
#mc_embed_signup .button:hover {background-color:#777;}
#mc_embed_signup .small-meta {font-size: 11px;}
#mc_embed_signup .nowrap {white-space:nowrap;}     
#mc_embed_signup .clear {clear:none; display:inline;}

#mc_embed_signup label {display:block; font-size:16px; padding-bottom:10px; font-weight:bold;}
#mc_embed_signup input.email {font-family:"Open Sans","Helvetica Neue",Arial,Helvetica,Verdana,sans-serif; font-size: 15px; display:block; padding:0 0.4em; margin:0 4% 10px 0; min-height:32px; width:58%; min-width:130px; border-radius: 3px;}
#mc_embed_signup input.button {display:block; width:35%; margin:0 0 10px 0; min-width:90px;}

#mc_embed_signup div#mce-responses {float:left; top:-1.4em; padding:0em .5em 0em .5em; overflow:hidden; width:90%;margin: 0 5%; clear: both;}
#mc_embed_signup div.response {margin:1em 0; padding:1em .5em .5em 0; font-weight:bold; float:left; top:-1.5em; z-index:1; width:80%;}
#mc_embed_signup #mce-error-response {display:none;}
#mc_embed_signup #mce-success-response {color:#529214; display:none;}
#mc_embed_signup label.error {display:block; float:none; width:auto; margin-left:1.05em; text-align:left; padding:.5em 0;}
